@import "../../../partials";
@import "../../../App.scss";

@mixin text-input {
    height: 30px;
    font-size: 20px;
    box-sizing:border-box
}

@mixin content-switch-button {
    height: 25px;
}

@mixin hero-button {
    height: 50px;
    width: 100%;
    background-color: royalblue;
    font-size: 18px;
    color: white;
}



.welcome-login-register-section {
    @include flex-display-row;
    height: calc(100vh - 50px);
    p {
        color: $primary-text-color;
    }
}

.welcome-hero-hero-container,
.welcome-hero-side-container {
    @include flex-display-column;
    margin: auto;
    justify-content: center;
}

.welcome-hero-hero-container {
    width: 65%;
    text-align: center;
    font-size: $primary-medium-font-size;
    height: 100%;
}

.welcome-hero-side-container {
    height: 100%;
    width: 35%;
    margin-left: 25px;
    margin-right: 100px;
    button {
        width: fit-content;
    }
}
